import React, { useEffect, useRef, useState } from "react";
import classNames from "classnames";
import { SectionProps } from "../../utils/SectionProps";
import ButtonGroup from "../elements/ButtonGroup";
import Button from "../elements/Button";
// import Image from "../elements/Image";
// import Modal from "../elements/Modal";
// import RadioPlayer from "./RadioPlayer";

import $ from 'jquery';

const propTypes = {
  ...SectionProps.types,
};

const defaultProps = {
  ...SectionProps.defaults,
}

const Hero = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {
  const [setVideomodalactive] = useState(false);

  const openModal = (e) => {
    e.preventDefault();
    setVideomodalactive(true);
  };

  const closeModal = (e) => {
    e.preventDefault();
    setVideomodalactive(false);
  };

  const outerClasses = classNames(
    "hero section center-content",
    topOuterDivider && "has-top-divider",
    bottomOuterDivider && "has-bottom-divider",
    hasBgColor && "has-bg-color",
    invertColor && "invert-color",
    className
  );

  const innerClasses = classNames(
    "hero-inner section-inner",
    topDivider && "has-top-divider",
    bottomDivider && "has-bottom-divider"
  );

  const lunaRadioRef = useRef(null);

  useEffect(() => {
    if (typeof $.fn.lunaradio === "undefined") {
      const script = document.createElement('script');
      if (window.$ || window.jQuery) {
        console.log("jQuery está cargado");
      } else {
        console.log("jQuery no está cargado");
      }
      script.src = "/express-fm/lunaradio-sincors.js";
      script.async = true;
      document.body.appendChild(script);

      script.onload = () => {
        if (lunaRadioRef.current) {
          $(lunaRadioRef.current).lunaradio({
            userinterface: "big",
            backgroundcolor: "ffffff",
            fontcolor: "#ffffff",
            hightlightcolor: "#000000",
            fontname: "Calibri",
            googlefont: "Unica+One&display=swap",
            fontratio: "0.6",
            radioname: "Radio Express",
            scroll: "true",
            coverimage: "/express-fm/logo.jpg",
            onlycoverimage: "false",
            coverstyle: "animated",
            usevisualizer: "real",
            visualizertype: "4",
            metadatatechnic: "",
            ownmetadataurl: "",
            streamurl: "https:sonic.sistemahost.es:7118",
            streamtype: "icecast2",
            icecastmountpoint: "/live",
            displayliveicon: "true",
            shoutcastpath: "/stream",
            shoutcastid: "1",
            streamsuffix: "",
            radionomyid: "",
            radionomyapikey: "",
            radiojarid: "",
            radiocoid: "",
            itunestoken: "1000lIPN",
            metadatainterval: "5000",
            volume: "75",
            debug: "false",
            autoplay: "true"
          });
        }
      };

      return () => {
        document.body.removeChild(script);
      };
    }
  }, []);

  return (
    <section {...props} className={outerClasses}>
      <div className="container-sm">
        <div className={innerClasses}>
          <div className="hero-content">
            <h1
              className="mt-0 mb-16 reveal-from-bottom"
              data-reveal-delay="200"
            >
              Radio Expres <p className="text-color-primary">98.9</p>
            </h1>
            <div className="container-xs">
              <p
                className="m-0 mb-32 reveal-from-bottom"
                data-reveal-delay="400"
              >
                Con el Rock and Pop de tu mejor epoca!
              </p>
              <div>
              </div>
              <div className="reveal-from-bottom" data-reveal-delay="600">
                <div className="container">
                  <div id="lunaradio" ref={lunaRadioRef} style={{width: '600px', height: '500px', background: '#000000 url(/express-fm/portada.jpg) bottom / cover no-repeat' }}>
                    loading...
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

Hero.propTypes = propTypes;
Hero.defaultProps = defaultProps;
export default Hero;
